@import 'fonts.scss';

header{
   font-size: 10px;
  }
  .first-nav{
    width: 100%;
    min-height: 40px;
    background-color: #333333;
    font-family: $Proxima;
    font-weight: 700;
    color: #777777;
    font-size: 1.5em;
    .fnav-wrapper{
      max-width: 1170px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: auto;
      .currency{
        width: auto;
        cursor: pointer;
        i{
          padding-left: 4px;
        }
        #currency-type:hover{
          transition: color 0.2s ease;
          color: #b8b8b8;
        }
      }
      .user-interaction-menu{ 
      width: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      .user-account-buttons{
        display: flex;
        justify-content: space-between;
        a:nth-child(2) {
         padding-left: 2em;
         }
         a:hover{
          transition: color 0.2s ease;
          color: #b8b8b8;
        }
      }
      #shopping-cart{ 
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        height: 3em;
        width: 10em;
        margin: 0 0 0 3em;
        background-color: #00c8c8 ;
        #cart-status{
          padding-left: 1.5em;
          padding-right: 0.3em;
        }
        &:hover{
          transition: color 0.2s ease;
          color: #333333;
        }
      }
      }
    }
  }
  .second-nav{
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 110px;
    background-color: #f8f8f8;
    font-family: $Montserrat;
    font-size: 1em;
    font-weight: 500;
    .secondNav-wrapper{
      position: relative;
      z-index: 100;
      width: 100%;
      margin: 0 5%;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;

      .goods-categories{
        position: relative;
        width: 100%;
        font-weight: 500;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
        a{
          display: inline-block;
          color: #222222;
          text-transform: uppercase;
          text-decoration: none;
          padding: 0 5em 0 0;
          font-weight: 500;
        }
        .menu-link{
          position: relative;
          display: inline-block;
          text-align: left;
          &:hover{
            color: #40cfcf;
          }
          i{
            display: inline-block;
            width: calc(100% - 5em);
            position: absolute;
            top: 1.2em;
          }
        }
        .goods-search{
          position: relative;
          vertical-align: middle;
          white-space: nowrap;
          padding: 0 0 0 0;
          margin-left: 3%;
          input#search{
            outline: none;
            height: 40px;
            width: 220px;
            border: 1px solid #e7e7e7;
            background-color: #f8f8f8;
            font-size: 1.2em;
            padding-left: 0.7em;
            input:placeholder-shown{
              color: #989898;
              padding-left: 10px;
            }
            &::-webkit-search-cancel-button,
            ::-webkit-search-results-button,
            ::-webkit-search-results-decoration { display: none; } 
          }
            .search-icon{
              position: absolute;
              cursor: pointer;
              left: 88%;
              top: 30%;
              z-index: 1;
              font-size: 1.1em;
              color: rgb(152, 152, 152);
            }
        }
      }
    }
  }
  .logo {
    display: none;
    width: auto;
    font-size: 2.4em;
    padding: 0 0 0 0;
    color: rgb(34, 34, 34);
    font-weight: 600;
    text-transform: uppercase;
    cursor: default;
    #fashion{
      font-weight: 300;
    }
  }
  #MenuToggle{
    display: none;
    position: fixed;
    top: 50px;
    left: 25px;
    z-index: 15;
    -webkit-user-select: none;
    user-select: none;
    a{
      text-decoration: none;
      color: #232323;
      transition: color 0.3s ease;
      &:hover{
        color: #0ea8a8;
      }
    }
    input{
      position: absolute;
      display: block;
      width: 40px;
      height: 32px;
      top: -7px;
      left: -5px;
      cursor: pointer;
      opacity: 0; /* hide this */
      z-index: 10; /* and place it over the hamburger */
      -webkit-touch-callout: none;
    }
    span {
      display: block;
      width: 33px;
      height: 4px;
      margin-bottom: 5px;
      position: relative;  
      background: #cdcdcd;
      border-radius: 1px;
      z-index: 1;
      transform-origin: 4px 0px;
      transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                  background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                  opacity 0.55s ease;
    }
    span:first-child {
      transform-origin: 0% 0%;
    }
    span:nth-last-child(2) {
      transform-origin: 0% 100%;
    }
    input:checked ~ span {
      opacity: 1;
      transform: rotate(45deg) translate(-2px, -1px);
      background: #232323;
    }
    input:checked ~ span:nth-last-child(3) {
    opacity: 0;
    transform: rotate(0deg) scale(0.2, 0.2);
    }
    input:checked ~ span:nth-last-child(2) {
      transform: rotate(-45deg) translate(0, -1px);
    }
    input:checked ~ ul {
      transform: none;
    }
  }
  #menu {
    position: absolute;
    width: 40vw;
    height: 100vh;
    margin: -100px 0 0 -50px;
    padding: 0 0 0 50px;
    padding-top: 125px;
    background-color: #ededed;
    list-style-type: none;
    transform-origin: 0% 0%;
    transform: translate(-100%, 0);
    transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
    li{
      padding: 1em 0;
      .goods-search-responsive{
        position: relative;
        vertical-align: middle;
        background-color: black;
        white-space: nowrap;
        padding: 0 0 0 0;
        margin-left: 3%;
        input#search-responsive{
