.intro{
    position: relative;
    width: 100%;
    min-height: 450px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    font-family: 'Montserrat', sans-serif;
    &-vignette{
        position: absolute;
        z-index: 3;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        width: 100%;
        height: 100%;
        background-color: rgba($color: #333333, $alpha: .75);
        text-transform: uppercase;
        font-size: $px450;
        line-height: 450px;
        &__text{
            display: flex;
            justify-content: space-between;
            height: 409.5px;
            font-weight: 700;
            width: calc((1535 / 2050) * 100vw);
            color: rgba($color: #ffffff, $alpha: 0.04);
        }
    }
    &-container{
        position: absolute;
        z-index: 5;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        &__title{
            text-transform: uppercase;
            font-size: $px48;
            line-height: 52px;
            font-weight: 300;
            color: #ffffff;
            margin-bottom: 30px;
            span{
                font-weight: 700;
            }
        }
        &__subtitle{
            text-transform: uppercase;
            font-size: $px30;
            line-height: 54px;
            font-weight: 400;
            color: #fff;
        span{
        color: #70c7c7;
        }
        
        }
    }
}


.container{
    width: 97vw;
    max-width: 1170px;
    margin: 70px auto;
  
}
.slider-inner{
    position:relative;
  display: flex;
  justify-content: space-between;
