    width:100%;

}

.arrow{
    width:100%;
    position:absolute;
    top:50%;
 
    }

.prev{
    left: 5px;
    position: absolute;
        z-index:5;
        cursor:pointer;
        width:45px;
        height:45px;
        display:flex;
        justify-content: center;
        align-items: center;
}

.prev:hover{
    color:#fff;
   background-color:#70c7c7;
    transition: 0.3s;
}

.next{
    font-size:40;
    right:5px;
    position: absolute;
    z-index:5;
    cursor:pointer;
    width:45px;
    height:45px;
    display:flex;
    justify-content: center;
    align-items: center;

}  
