.select{
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    position: relative;
    min-width: 200px;
    width: 20%;
    font-family: 'Monserrat',sans-serif;
    color: #333333;
  }
  
  .select-title{

        font-size: 13px;
        font-family: "Montserrat";
        color: rgb(86, 82, 84);
        text-transform: uppercase;
        line-height: 2.308; 
  }
  
  .select__text {
    position: relative;
    font-size: 13px;
  }
  .select__text span{
    position: absolute;
    top: 13.5px;
    right: 30px;
    cursor: pointer;
  }
  .select__input{
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    height: 35px;
    padding: 10px 45px 10px 15px;
    border: 2px solid #e3e3e3;
    color: #999999;
    cursor: pointer;
  }
  .select-list {
    box-sizing: border-box;
    display: none;
    position: absolute;
    list-style: none;
    width: 100%;
    padding: 5px 0;
    margin: 0;
    background-color: #ffffff;
    border: 2px solid #e3e3e3;
  }
  
  .select-list--active {
    display: block;
    z-index: 2;
    left: 0;
    top: 100%;
  }
  
  .select-list__item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 5px 15px;
    text-align: right;
    cursor: pointer;
  }
  
  .select-list__item:not(:first-child) {
    margin-top: 10px;
  }
  
  .select-list__item:hover {
    background-color: #e3e3e3;
  }
  
  .color-list__item-color {
    position: relative;
    flex-shrink: 0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-right: 15px;
  }
  
  .select-list__item--active .color-list__item-color::before {
    content: "";
    position: absolute;
    top: -4px;
    right: -4px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    border: 2px solid #0e0e35;
  }
  
  .size-list__item {
    padding: 5px 30px;
    cursor: pointer;
  }
  
  .size-list__item:not(:first-child) {
    margin-top: 10px;
  }
  
  .size-list__item:hover {
    background-color: #e3e3e3;
  }

.select-qty{
    &__input{
        box-sizing: border-box;
        width: 60px;
        height: 40px;
        border: 2px solid #e3e3e3;
        color: #999999;
        padding: 0 30px 0 10px;
    }
}
.qty-inner{
    position: relative;
    &__arrows{
        box-sizing: border-box;
        position: absolute;
        top: 0; right: 10px;
        width: 20px;
        height: 100%;
        padding: 7px 0;
        font-size: 12px;
        color: #555555;
    }
    &__arrow-up,
    &__arrow-down{
        display: flex;
        justify-content: center;
        align-items: center;
        height: 50%;
        cursor: pointer;
    }
}

